.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0 50px;

  box-shadow: 0 2px 4px 0 #0000000d;
}

.logo {
  display: flex;
  align-items: center;
}

.nav {
  &__list {
    display: flex;

    font-weight: 500;
    font-size: 12px;
    list-style: none;
  }

  &__item:not(:first-child) {
    margin-left: 20px;
  }

  &__link {
    display: block;

    line-height: 60px;
    text-transform: uppercase;
    text-decoration: none;
    color: $main-text-color;

    &:hover {
      color: $hover-text-color;
    }
  }
}

.is-active {
  position: relative;

  color: $hover-text-color;
}

.is-active::after {
  content: "";
  position: absolute;
  bottom: 0;

  display: block;
  height: 4px;
  width: 100%;

  border-radius: 8px;
  background-color: $hover-text-color;
}
